
			import * as THREE from 'three'
			import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
			import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'
			import { RGBELoader } from 'three/examples/jsm/loaders/RGBELoader.js'
			import { RoughnessMipmapper } from 'three/examples/jsm/utils/RoughnessMipmapper.js'

			import * as dat from 'dat.gui'
			import { RectAreaLightHelper } from 'three/examples/jsm/helpers/RectAreaLightHelper.js'
            import { WebGLRenderTarget } from 'three'
            import {Text} from 'troika-three-text'


			let camera, scene, renderer;

            let myText = new Text();

			init();
			render();

			function init() {

				const container = document.createElement( 'div' );
				document.body.appendChild( container );

				camera = new THREE.PerspectiveCamera( 45, window.innerWidth / window.innerHeight, 0.25, 50 );
				camera.position.set( -11.411290079421022, 4.422028698375285, 0.5044110160965234 );
                
				// -11.411290079421022, y: 4.422028698375285, z: 0.5044110160965234}


				scene = new THREE.Scene();



                // Create:
              
                scene.add(myText)

                // Set properties to configure:
                myText.text = 'Flow Rate : 840'
                myText.fontSize = 4
                myText.position.z =  4
                myText.position.x =  0
                myText.position.y =  8
				// myText.rotateY
         

                myText.color = 0xFFFFFF

                console.log(myText.text);
                console.log(myText.orientation);

           

				new RGBELoader()
					.setDataType( THREE.UnsignedByteType )
					.setPath( 'textures/equirectangular/' )
					.load( 'royal_esplanade_1k.hdr', function ( texture ) {
					// .load( 'reinforced_concrete_02_1k.hdr', function ( texture ) {

						const envMap = pmremGenerator.fromEquirectangular( texture ).texture;

						scene.background = envMap;
						scene.environment = envMap;

						texture.dispose();
						pmremGenerator.dispose();

						render();

						// model

						// use of RoughnessMipmapper is optional
						const roughnessMipmapper = new RoughnessMipmapper( renderer );

						const loader = new GLTFLoader().setPath( 'models/gltf/' );
						loader.load( 'isometric_factory_B.gltf', function ( gltf ) {

							gltf.scene.traverse( function ( child ) {

								if ( child.isMesh ) {

									// TOFIX RoughnessMipmapper seems to be broken with WebGL 2.0
									 //roughnessMipmapper.generateMipmaps( child.material );
                                    
								}
                                

							} );

                            gltf.scene.scale.set(0.85,0.85,0.85);
							gltf.scene.rotateY = Math.PI * 180;
                            
							scene.add( gltf.scene );
                            // camera.lookAt(gltf.scene)
							console.log(camera)
							roughnessMipmapper.dispose();

							render();

						} );

					} );

				renderer = new THREE.WebGLRenderer( { antialias: true } );
				renderer.setPixelRatio( window.devicePixelRatio );
				renderer.setSize( window.innerWidth, window.innerHeight );
				renderer.toneMapping = THREE.ACESFilmicToneMapping;
				renderer.toneMappingExposure = 1;
				renderer.outputEncoding = THREE.sRGBEncoding;
				container.appendChild( renderer.domElement );

				const pmremGenerator = new THREE.PMREMGenerator( renderer );
				pmremGenerator.compileEquirectangularShader();

				const controls = new OrbitControls( camera, renderer.domElement );
				controls.addEventListener( 'change', render ); // use if there is no animation loop
				controls.minDistance = 5;
				controls.maxDistance = 100;
				controls.target.set( 0, 0, - 0.2 );
				controls.update();

				window.addEventListener( 'resize', onWindowResize );

			}

			function onWindowResize() {

				camera.aspect = window.innerWidth / window.innerHeight;
				camera.updateProjectionMatrix();

				renderer.setSize( window.innerWidth, window.innerHeight );

				render();

			}

			function getRandomArbitrary(min, max) {
				return Math.random() * (max - min) + min;
			  }

			//

			function render() {

				var d = new Date();
                var n = d.getTime();
                myText.text = n;
				myText.text = "Pressure : " + getRandomArbitrary(800,909);;
				myText.sync()
				console.log(camera)
                
                
				renderer.render( scene, camera );
				
			}